$color-accent: #007188;
$color-text: #1D253C;
$color-theme: #F46752;
$color-theme-hover-5: darken($color-theme, 5);
$color-theme-hover-10: darken($color-theme, 10);

.Bello {
    --color-accent: #{$color-accent};
    --color-text: #{$color-text};
    --color-theme: #{$color-theme};
    --color-theme-hover-5: #{$color-theme-hover-5};
    --color-theme-hover-10: #{$color-theme-hover-10};
    --width-container: 1440px;
    --padding-container: 0 60px;


    color: var(--color-text);
    box-sizing: border-box;
    font-family: 'Work Sans', sans-serif;    
}